body {
  margin: 0;
  padding: 0;
  font-family: Verdana;
}

.current {
  background: #033;
  border-radius: 5px;
  padding: 2px;
}

.txt-small {
  font-size: 14px;
}

.txt-b {
  font-weight: bold;
}

.ms-15 {
  margin-left: 15px;
}

.ms-170 {
  margin-left: 170px;
}

.ms-60 {
  margin-left: 60px;
}

.ms-262 {
  margin-left: 262px;
}

.ms-220 {
  margin-left: 220px;
}

.ms-285 {
  margin-left: 285px;
}

.mt-8 {
  margin-top: 8px;
}

.mt-5 {
  margin-top: 5px;
}


.hidden {
  display: none;
}

nav.main-nav {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 17px;
  background: #005050;
  z-index: 10;
}

nav.main-nav a {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  justify-items: center;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 17px;
  padding: 4px 3px;
}

nav.main-nav a:hover,
nav.main-nav a:focus {
  background: white;
  color: blue;
  text-shadow: rgb(0, 255, 13) 2px 2px 2px;
  border-radius: 5px;
}

/* for main top header */
header.main-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  align-items: center;
  padding: 15px;
  background: #088;
  color: white;
  font-weight: bold;
  font-size: 22px;
  margin-top: 86px;
}

header.main-header div.pContainer {
  width: 80vw;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  z-index: 0;
}

header.main-header div.pContainer p {
  position: relative;
  height: 40px;
  overflow: visible;
  white-space: nowrap;
  animation-name: textMover;
  animation-duration: 30s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

div#login-profile-name {
  font-size: 15px;
}

div.login-logo {
  margin-left: 16px;
  margin-right: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header.main-header button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 4vw;
  color: white;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

header.main-header button i {
  font-size: 28px;
}


@keyframes textMover {
  from {
    left: 100vw;
  }

  to {
    left: -120vw;
  }
}

@keyframes textMover1 {
  from {
    left: 100vw;
  }

  to {
    left: -220vw;
  }
}

@keyframes textMover2 {
  from {
    left: 100vw;
  }

  to {
    left: -350vw;
  }
}

/* side bar of the website */

section.aside-wrapper {
  display: none;
  position: fixed;
  background-color: #0885;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 12;
  /* opacity: 0;
  transition: display 1s, opacity 3s; */
}

section.aside-wrapper.open {
  display: initial;
  opacity: 1;
}

aside.main-aside {
  display: flex;
  flex-direction: column;
  background: #088;
  padding: 19px;
  width: 20vw;
  float: right;
  height: 100vh;
}

div#close-menu {
  padding: 3px 5px;
  background-color: red;
  color: white;
  font-weight: bold;
  font-size: 19px;
  width: 25px;
  text-align: center;
  border-radius: 20%;
  cursor: pointer;
}

aside.main-aside section {
  display: flex;
  flex-direction: column;
}

aside.main-aside section p {
  padding-bottom: 2px;
  border-bottom: 1px #FFF5 solid;
}

aside.main-aside a,
aside.main-aside section a {
  text-align: left;
  text-decoration: none;
  color: #fff;
  margin-top: 11px;
}


/* section subscribe */

section.subscribe-wrapper {
  background: radial-gradient(circle at 46%, rgb(18, 18, 158), rgb(14, 121, 156), rgb(141, 118, 40), blue);
}

section.subscribe {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  margin-left: 10vw;
  padding: 19px 22px;
}

section.subscribe p {
  margin-left: 15px;
  font-size: 24px;
  padding: 11px;
  background-color: rgba(255, 255, 0, 0.26);
  border-radius: 9px;
  color: white;
}

section.subscribe input {
  margin-left: 15px;
  font-size: 23px;
  padding: 11px;
  border-radius: 9px;
  border: green 1px solid;
  outline: none;
  width: 40vw;
}

section.subscribe button {
  margin-left: 15px;
  background-color: rgba(255, 1, 1, 0.726);
  color: white;
  font-size: 23px;
  padding: 11px;
  border-radius: 9px;
  border: rgb(0, 19, 128) 1px solid;
  outline: none;
  cursor: pointer;
}

section.subscribe button:hover,
section.subscribe button:focus {
  background-color: rgb(0, 255, 255);
  color: #000;
}

::selection {
  color: #fff;
  background: #005050;
  background-color: #005050;
}

