footer.footer {
    background: #055;
    padding: 2vw 4.2vw;
    width: auto;
}

footer.footer section.container-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

footer.footer section.container-footer section {
    display: flex;
    flex-direction: column;
}

footer.footer section.container-footer section p {
    border-bottom: 1px #099 solid;
    color: white;
}

footer.footer section.container-footer section a {
    text-align: left;
    text-decoration: none;
    color: #fff;
    margin-top: 11px;
    font-size: 12px;
}

footer p.copyright {
    text-align: center;
    margin-top: 28px;
    color: #fff;
    border-top: 2px lightblue dotted;
    padding-top: 5px;
    padding-bottom: 5px;
    border-bottom: 2px lightblue dotted;
}

@media (max-width: 890px) {
    footer.footer {
        padding: 2vw 3vw;
    }

    footer.footer section.container-footer section {
        flex-direction: column;
    }

    footer.footer section.container-footer section p {
        font-size: 17px;
    }

    footer.footer section.container-footer section a {
        font-size: 15px;
    }
}

@media (max-width: 670px) {

    footer.footer section.container-footer {
        display: grid;
        grid-template-columns: auto auto;
        justify-content: space-around;
    }

    footer.footer section.container-footer section p {
        font-size: 16px;
    }

    footer.footer section.container-footer section a {
        font-size: 14px;
    }
}

@media (max-width: 490px) {

    footer.footer section.container-footer {
        display: flex;
        flex-direction: column;
    }

    footer.footer section.container-footer section p {
        font-size: 15px;
    }

    footer.footer section.container-footer section a {
        font-size: 13px;
    }
}

@media (max-width: 390px) {

    footer.footer section.container-footer section p {
        font-size: 14px;
    }
}