/* 
		style for the repo table
*/
/* 
	style for the main part of the page
*/

.lbl {
    font-weight: bold;
    margin-bottom: 10px;
    background-color: teal;
    color: white;
    padding: 7px 9px;
    border: 2px solid rgba(0, 0, 255, 0.356);
    border-radius: 8px;
    width: fit-content;
}

section.info-repo,
section.input-section {
    margin: 30px 40px;
}

section.input-section input {
    border: rgba(0, 0, 255, 0.432) 2px solid;
    padding: 6px 8px;
    border-radius: 8px;
    outline: none;
    width: 60vw;
    min-width: 200px;
    max-width: 70vw;
}

section.input-section input[type="button"] {
    border: rgba(0, 0, 255, 0.363) 2px solid;
    padding: 7px 8px;
    cursor: pointer;
    background-color: blue;
    color: white;
    width: 20vw;
    min-width: 60px;
    max-width: 30vw;
    margin-left: 10px;
}

table {
    margin: 35px 40px;
    width: calc(100% - 80px);
}

table th {
    border: 1px gold solid;
    font-weight: bold;
    padding: 7px 9px;
    background-color: rgba(255, 217, 0, 0.493);
    text-align: center;
}

/* table tr.tr-loading {
    overflow: visible;
} */

table td {
    border: 1px rgb(255, 136, 0) solid;
    padding: 5px 9px;
}


table:has(tr > td.td-loading) th.th-loading,
table:has(tr > td.td-loading) td {
    width: 175px !important;
    overflow: visible;
    padding: 7px;
}

table td.td-loading {
    border: none !important;
}

.div-loading {
    font-size: 33px;
    width: 175px;
    overflow: hidden;
    animation-name: tdLoading;
    animation-duration: 1s;
    animation-iteration-count: 60;

}


@keyframes tdLoading {
    0% {
        width: 0px;
        color: white;
    }

    100% {
        width: 175px;
        color: black;
    }
}

/* 
		max width 590px
*/

@media (max-width: 590px) {

    section.info-repo,
    section.input-section {
        margin: 30px 20px;
    }

    section.input-section p {
        background-color: teal;
        color: white;
        padding: 7px 9px;
        border: 2px solid rgba(0, 0, 255, 0.356);
        border-radius: 8px;
        width: 240px;
    }

    section.input-section input {
        border: rgba(0, 0, 255, 0.432) 2px solid;
        padding: 6px 8px;
        border-radius: 8px;
        outline: none;
    }

    section.input-section input[type="button"] {
        border: rgba(0, 0, 255, 0.363) 2px solid;
        padding: 7px 8px;
        cursor: pointer;
        background-color: blue;
        color: white;
    }

    section#tableContainer {
        overflow-x: scroll;
    }

    table {
        margin: 35px 20px;
    }

    table th {
        border: 1px green solid;
        font-weight: bold;
        background-color: rgba(0, 255, 0, 0.493);
        text-align: center;
        font-size: 12px;
    }

    table td {
        border: 1px rgb(30, 255, 0) solid;
        padding: 1px 1px;
        font-size: 11px;
    }
}

