/* 

	style for the projects

*/

article.group-projects {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* display: grid;
	grid-template-columns: auto auto auto; */
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
}

section.project-header {
    border-bottom: 6px dotted rgba(29, 131, 8, 0.952);
}

section.project-header h1,
section.project-header h2 {
    text-align: center;
}

p.top-paragraph {
    margin: 15px 25px;
    line-height: 26px;
}

a {
    color: #2989fe;
}

p.top-paragraph span {
    background: #000;
    color: #fff;
    border-radius: 5px;
    font-weight: bold;
    padding: 2px 3px;
}

section.direct-project {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid #14acf3;
    box-shadow: 0 0 15px 2px #2989fe;
    border-radius: 2vw;
    background: #2181fe;
    margin-bottom: 20px;
    margin-top: 20px;
    width: 300px;
    height: 300px;
    margin-right: 10px;
}

section.direct-project:hover {
    box-shadow: 0 0 10px 2px #2f2f2d;
    background: #009090;
    border: 1px solid #7d7d7d;
}

section.project-description {
    background: #0002;
    color: white;
    padding: 0px 4px;
    border-radius: inherit;
    text-align: center;
    height: inherit;
}

section.project-description:hover {
    background-color: #0003;
}

section.project-description p:first-child {
    background-color: #0004;
    margin-left: 14%;
    margin-right: 14%;
    padding: 3% 4%;
    border-radius: inherit;
    font-weight: bold;
    border: 2px #0002 solid;
}

section.project-description p:first-child:hover {
    background-color: #fff3;
}

section.project-sources {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    align-items: center;
}

section.project-sources h2 {
    color: #fff;
    background-color: #0003;
    cursor: pointer;
    border-radius: 9px;
    padding: 4px 10px;
    margin: 3px 0px;
    font-size: 15px;
}

section.project-sources h2 a {
    color: #fff;
    text-decoration: none;
}

section.project-sources h2:hover {
    background-color: #fff5;
    color: #003030;
}

section.project-sources h2 a:hover {
    color: #003030;
}

.android-app {
    border-radius: 2vw;
    margin-bottom: 20px;
    margin-top: 20px;
    margin-right: 5px;
    width: 300px;
    height: 300px;
    box-shadow: 0px 0px 5px 2px rgb(17, 215, 209);
}

.android-app-logo {
    width: 120px;
    height: 120px;
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 9px;
}

