/**
		media queries

*/
@media (max-width: 390px) and (min-width: 224px) {
    article.group-projects {
        display: grid;
        grid-template-columns: auto;
        justify-content: space-around;
    }
}

@media (max-width: 490px) and (min-width: 390px) {
    article.group-projects {
        display: grid;
        grid-template-columns: auto;
        justify-content: space-around;
    }
}

@media (max-width: 724px) and (min-width: 490px) {
    article.group-projects {
        display: grid;
        grid-template-columns: auto;
        justify-content: space-around;
    }
}

@media (max-width: 1216px) and (min-width: 724px) {
    article.group-projects {
        display: grid;
        grid-template-columns: auto auto;
        justify-content: space-around;
    }
}